import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Classfiy from '../views/Classfiy.vue';
import Shopping from '../views/Shopping.vue';
import Search from '../views/Search.vue';

VueRouter.prototype.back = false;
VueRouter.prototype.goBack = function goBack() {
  this.back = true;
  this.go(-1);
};
Vue.use(VueRouter);

const routes = [{
  path: '/home',
  component: Home,
  children: [{
    path: 'Classfiy',
    name: 'classfiy',
    component: Classfiy,
  },
  {
    path: 'Shopping',
    component: Shopping,
  },
  ],
},
{
  path: '/search',
  name: 'search',
  component: Search,
},
{
  path: '*',
  redirect: '/home/classfiy',
},
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
});

export default router;
